<template>
  <div class="home">
    <div class="container" align="center">
      <h1 class="heading">P2P Media example</h1>
      <p class="note">Enter remote peer ID to call.</p>
      <div class="p2p-media">
        <div class="remote-stream">
          <video id="js-remote-stream" muted></video>
        </div>
        <div class="local-stream">
          <video id="js-local-stream" muted></video>
          <p>
            Your ID:
            <span id="js-local-id"></span>
          </p>
          
          <v-text-field type="text" placeholder="Remote Peer ID" id="js-remote-id" />
          <v-btn id="js-call-trigger">Call</v-btn>
          <v-btn id="js-close-trigger">Close</v-btn>
          <v-btn id="js-muted-trigger">mic mute :WIP</v-btn>
        </div>
      </div>
      <p class="meta" id="js-meta"></p>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
//import HelloWorld from "@/components/HelloWorld.vue";

import Peer from 'skyway-js';
window.__SKYWAY_KEY__ = 'f2ed2aa7-54ce-4b4d-8710-625e8d8d886d';

window.onload = ()=>{
  const localVideo = document.getElementById('js-local-stream');
  const localId = document.getElementById('js-local-id');
  const callTrigger = document.getElementById('js-call-trigger');
  const closeTrigger = document.getElementById('js-close-trigger');
  const remoteVideo = document.getElementById('js-remote-stream');
  const remoteId = document.getElementById('js-remote-id');
  const meta = document.getElementById('js-meta');
  const sdkSrc = document.querySelector('script[src*=skyway]');

  meta.innerText = `
    UA: ${navigator.userAgent}
    SDK: ${sdkSrc ? sdkSrc.src : 'unknown'}
  `.trim();

  let localStream;
  navigator.mediaDevices
    .getUserMedia({
      audio: true,
      video: true,
      echoCancellationType: "browser"
    })
    .then(stream => {
      localStream = stream;
    })
    .catch(console.error);

  // Render local stream
  localVideo.muted = true;
  localVideo.playsInline = true;
  try {
    localVideo.srcObject = localStream;
  } catch (error) {
    console.log(localStream);
    localVideo.src = window.URL.createObjectURL(localStream);
  }
  localVideo.play().catch(console.error);

  const peer = (window.peer = new Peer({
    key: window.__SKYWAY_KEY__,
    debug: 3,
  }));

  // Register caller handler
  callTrigger.addEventListener('click', () => {
    // Note that you need to ensure the peer has connected to signaling server
    // before using methods of peer instance.
    if (!peer.open) {
      return;
    }

    const mediaConnection = peer.call(remoteId.value, localStream);

    mediaConnection.on('stream',stream => {
      // Render remote stream for caller
      remoteVideo.srcObject = stream;
      remoteVideo.playsInline = true;
      remoteVideo.play().catch(console.error);
    });

    mediaConnection.once('close', () => {
      remoteVideo.srcObject.getTracks().forEach(track => track.stop());
      remoteVideo.srcObject = null;
    });

    closeTrigger.addEventListener('click', () => mediaConnection.close(true));
  });

  peer.once('open', id => (localId.textContent = id));

  // Register callee handler
  peer.on('call', mediaConnection => {
    mediaConnection.answer(localStream);

    mediaConnection.on('stream', stream => {
      // Render remote stream for callee
      remoteVideo.srcObject = stream;
      remoteVideo.playsInline = true;
      remoteVideo.play().catch(console.error);
    });

    mediaConnection.once('close', () => {
      remoteVideo.srcObject.getTracks().forEach(track => track.stop());
      remoteVideo.srcObject = null;
    });

    closeTrigger.addEventListener('click', () => mediaConnection.close(true));
  });

  peer.on('error', console.error);

}

export default {
  name: "Home",
  components: {
    // HelloWorld
  },
  data: ()=>({
    
  })
};
</script>
